import React from "react";
import { Col, Container, Row } from "reactstrap";

const ServiceCategories = () => {
  return (
    <>
      <Container className="t-color service-category">
        <Row>
          <Col className="col-12 col-sm-6 col-md-3 my-3">
            <div className="s-back text-center p-2">
              <img
                src="/images/ux-design.jpg"
                className="my-5"
                alt="UI/UX Design"
              />
              <h3 className="mb-5 f-size-20">UI/UX Design</h3>
            </div>
          </Col>
          <Col className="col-12 col-sm-6 col-md-3 my-3">
            <div className="s-back text-center p-2">
              <img
                src="/images/web-development.jpg"
                className="my-5"
                alt="Web Development"
              />
              <h3 className="mb-5 f-size-20">Web Development</h3>
            </div>
          </Col>
          <Col className="col-12 col-sm-6 col-md-3 my-3">
            <div className="s-back text-center p-2">
              <img src="/images/blog.jpg" className="my-5" alt="Blog" />
              <h3 className="mb-5 f-size-20">Blog</h3>
            </div>
          </Col>
          <Col className="col-12 col-sm-6 col-md-3 my-3">
            <div className="s-back text-center p-2">
              <img src="/images/testing.jpg" className="my-5" alt="Testing" />
              <h3 className="mb-5 f-size-20">Testing</h3>
            </div>
          </Col>
          <Col className="col-12 col-sm-6 col-md-3 my-3">
            <div className="s-back text-center p-2">
              <img
                src="/images/apps-development.jpg"
                className="my-5"
                alt="Apps Development"
              />
              <h3 className="mb-5 f-size-20">Apps Development</h3>
            </div>
          </Col>
          <Col className="col-12 col-sm-6 col-md-3 my-3">
            <div className="s-back text-center p-2">
              <img
                src="/images/wireframe-design.jpg"
                className="my-5"
                alt="Wire Frame Design"
              />
              <h3 className="mb-5 f-size-20">Wire Frame Design</h3>
            </div>
          </Col>
          <Col className="col-12 col-sm-6 col-md-3 my-3">
            <div className="s-back text-center p-2">
              <img
                src="/images/product-design.jpg"
                className="my-5"
                alt="Product Design"
              />
              <h3 className="mb-5 f-size-20">Product Design</h3>
            </div>
          </Col>
          <Col className="col-12 col-sm-6 col-md-3 my-3">
            <div className="s-back text-center p-2">
              <img
                src="/images/logo-design.jpg"
                className="my-5"
                alt="Logo Design"
              />
              <h3 className="mb-5 f-size-20">Logo Design</h3>
            </div>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default ServiceCategories;
