import React from "react";
import { Button, Container } from "reactstrap";

const BlogBanner = () => {
  return (
    <>
      <Container fluid className="m-0 p-0">
        <div className="blogbanner-image">
          <img src="/images/blog-banner.jpg" alt="Blog" />
        </div>
      </Container>
    </>
  );
};

export default BlogBanner;
