import React from "react";
import Base from "../Base";
import BlogBanner from "../shared-components/BlogBanner";
import BlogTech from "../shared-components/BlogTech";
import ServiceCategories from "../shared-components/ServiceCategories";

const Blog = () => {
  return (
    <>
      <Base>
        <div className="container-fluid px-0">
          <BlogBanner />
          <BlogTech />
          <ServiceCategories />
        </div>
      </Base>
    </>
  );
};

export default Blog;
